<template>
  <div class="stack-small">
    <div v-if="!isEditing">
      <div class="custom-checkbox">
        <input
          type="checkbox"
          class="checkbox"
          :id="id"
          :checked="isDone"
          @change="$emit('checkbox-changed')" />
        <label :for="id" class="checkbox-label">{{ label }}</label>
      </div>
      <div class="btn-group">
        <!-- 使用 ElButton -->
        <ElButton type="button" class="btn" ref="editButton" @click="toggleToItemEditForm">
          Edit <span class="visually-hidden">{{ label }}</span>
        </ElButton>
        <ElButton type="button" class="btn btn__danger" @click="deleteToDo">
          Delete <span class="visually-hidden">{{ label }}</span>
        </ElButton>
      </div>
    </div>
    <to-do-item-edit-form
      v-else
      :id="id"
      :label="label"
      @item-edited="itemEdited"
      @edit-cancelled="editCancelled">
    </to-do-item-edit-form>
  </div>
</template>

<script>
import ToDoItemEditForm from "./ToDoItemEditForm";
import { ElButton, ElMessage } from 'element-plus';

export default {
  name: 'ToDoItem',
  components: {
    ToDoItemEditForm,
    ElButton, // 确保在这里注册组件，并在模板中使用
  },
  props: {
    id: {
      type: String,
      required: true,
    },
    label: {
      type: String,
      required: true,
    },
    done: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      isEditing: false,
    };
  },
  computed: {
    isDone() {
      return this.done;
    },
  },
  methods: {
    handleClick() {
      // 使用 ElMessage 显示消息
      ElMessage({
        message: '按钮被点击了!',
        type: 'success'
      });
    },

    deleteToDo() {
      this.$emit("item-deleted");
    },

    toggleToItemEditForm() {
      console.log(this.$refs.editButton); // For debugging purposes
      this.isEditing = true;
    },

    itemEdited(newLabel) {
      this.$emit("item-edited", newLabel);
      this.isEditing = false;
      this.focusOnEditButton();
    },

    editCancelled() {
      this.isEditing = false;
      this.focusOnEditButton();
    },

    focusOnEditButton() {
      this.$nextTick(() => {
        const editButtonRef = this.$refs.editButton;
        if (editButtonRef) {
          editButtonRef.focus();
        }
      });
    },
  },
};
</script>

<style scoped>
.custom-checkbox > .checkbox-label {
  font-family: Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  font-size: 16px;
  font-size: 1rem;
  line-height: 1.25;
  color: #0b0c0c;
  display: block;
  margin-bottom: 5px;
}
</style>
